const initialState = {
    firstName: 'Alex',
    lastName: 'Vasch',
    avatar: [],
};

const editUser = (state = initialState, action) => {
    switch (action.type) {
        case 'EDIT_FIRST_NAME':
            return {
                ...state,
                firstName: action.payload,
            };
        case 'EDIT_LAST_NAME':
            return {
                ...state,
                lastName: action.payload,
            };
        case 'EDIT_AVATAR':
            return {
                ...state,
                avatar: action.payload,
            };

        default:
            return state;

    }




};

export default editUser;