import React from 'react'
import { setEditFirstName, setEditLastName } from './../../redux/actions/editUser';
import { useDispatch } from 'react-redux';

const DataTab = () => {
    const dispatch = useDispatch();
    const [firstName, setFirstName] = React.useState('')
    const [lastName, setLastName] = React.useState('')

    const setEditUser = () => {
        if (firstName !== '') {
            dispatch(setEditFirstName(firstName));
            setFirstName('')
        }
        if (lastName !== '') {
            dispatch(setEditLastName(lastName));
            setLastName('')
        }
    }

    return (
        <div className="personal__frame-cont">
            <div className="personal__mobile-menu-cont">
                <p className="personal__mobile-menu-text"> МЕНЮ</p>
                <div className="personal__mobile-menu">
                    <div className="personal__mobile-menu-inner"></div>
                </div>
            </div>
            <h3 className="personal__form-title">Данные</h3>
            <div className="personal__line"></div>
            <form action="" className="personal__form">
                <p className="personal__form-text personal__form-text-data">Имя</p>
                <input type="text" className="personal__input" value={firstName} onChange={(e) => setFirstName(e.target.value)} placeholder="Имя" />
                <p className="personal__form-text">Фамилия</p>
                <input type="text" className="personal__input" value={lastName} onChange={(e) => setLastName(e.target.value)} placeholder="Фамилия" />
                <p className="personal__form-text">Номер телефона</p>
                <input type="text" className="personal__input" placeholder="Номер телефона" />
                <input type="button" value="COXРАНИТЬ" className="personal__button" onClick={() => setEditUser()} />
            </form>
        </div>
    )
}

export default DataTab
