export const realty = (realtyItems) => ({
  type: 'SET_REALTY_ITEMS',
  payload: realtyItems,
});

export const editRealty = (realtyItems) => ({
  type: 'EDIT_REALTY_ITEMS',
  payload: realtyItems,
});

export const removeRealty = (realtyItems) => ({
  type: 'REMOVE_REALTY_ITEMS',
  payload: realtyItems,
});

