import React from 'react'
import accountStartImg from '../../assets/img/svg/account-start-1.svg'
import { useSelector } from 'react-redux';

const WelcomeTab = () => {
    const getEditUser = useSelector((state) => state.editUser);

    return (
        <div className="personal__img-cont">
            <div className="personal__mobile-menu-cont">
                <p className="personal__mobile-menu-text"> МЕНЮ</p>
                <div className="personal__mobile-menu">
                    <div className="personal__mobile-menu-inner"></div>
                </div>
            </div>
            <div className="vacancy__head account-main-i ">
                <img className="account-img-main" src={accountStartImg} alt="" />
            </div>
            <h3 className="personal__welcome">Добро пожаловать в личный кабинет <span
                className="personal__welcome--pink">PobytOK</span> </h3>
            <p className="personal__register-status">Вы зарегистрированы как <span
                className="personal__welcome--pink personal__welcome--pink-reg">Общий
                пользователь</span></p>
            <div className="personal__your-name">{getEditUser.firstName} {getEditUser.lastName}</div>
        </div>

    )
}

export default WelcomeTab
