export const setEditFirstName = (firstName) => ({
  type: 'EDIT_FIRST_NAME',
  payload: firstName,
});

export const setEditLastName = (lastName) => ({
  type: 'EDIT_LAST_NAME',
  payload: lastName,
});

export const setUserAvatar = (avatar) => ({
  type: 'EDIT_AVATAR',
  payload: avatar,
});

