import React from 'react'
import avatarDefault from '../../assets/img/svg/accaunt-cont-ava.svg'
import ImageUploading from 'react-images-uploading';
import { useDispatch } from 'react-redux';
import { setUserAvatar } from './../../redux/actions/editUser';
import { useSelector } from 'react-redux';

const ChangeAva = () => {
    const { avatar } = useSelector((state) => state.editUser);
    const dispatch = useDispatch();

    const onChange = imageList => {
        dispatch(setUserAvatar(imageList))
    };

    return (
        <> <p className="personal__less-title">Фото профиля</p>
            <ImageUploading value={avatar} onChange={onChange} dataURLKey="data_url"  >
                {({ imageList, onImageUpload, onImageUpdate, }) => (
                    <div >
                        {!avatar[0] && <button className='change-img-btn' onClick={onImageUpload}><img src={avatarDefault} className="personal__avatar" alt="avatar" /></button>}
                        {imageList.map((image, index) => (
                            <div key={index} className=" personal__ava-change-cont">
                                <img src={image['data_url']} className="personal__avatar" alt="avatar" />
                                <p className="personal__change-ava" onClick={() => onImageUpdate(index)}>ИЗМЕНИТЬ ФОТО</p>

                            </div>
                        ))}
                    </div>
                )}
            </ImageUploading> </>
    )
}

export default ChangeAva
