import React from 'react'
import ChangeAva from './ChangeAva'
import { useCookies } from 'react-cookie';

const ControlTab = () => {

    const [cookies, setCookie] = useCookies(['name']);
    const newName = 'myna'
    const setCoocie = () => {
        setCookie('name', newName, { path: '/' });
        console.log('12');
    }
    console.log(cookies);

    return (
        <div className="personal__frame-cont">
            <div className="personal__mobile-menu-cont">
                <p className="personal__mobile-menu-text"> МЕНЮ</p>
                <div className="personal__mobile-menu">
                    <div className="personal__mobile-menu-inner"></div>
                </div>
            </div>

            <h3 className="personal__form-title">Управление аккаунтом</h3>
            <div className="personal__line"></div>
            <ChangeAva />
            <p className="personal__less-title">Изменить пароль</p>

            <form action="" className="personal__form">
                <p className="personal__form-text">Текущий пароль</p>
                <input type="password" name="" className="personal__input" placeholder="Текущий пароль" autoComplete="on" />
                <p className="personal__form-text">Новый пароль</p>
                <input type="password" name="" className="personal__input" placeholder="Новый пароль" autoComplete="on" />
                <input type="button" value="COXРАНИТЬ" name="" className="personal__button" onClick={() => setCoocie()} />
            </form>


            <p className="personal__less-title">Сменить E-mail</p>

            <form action="" className="personal__form">
                <p className="personal__form-text">Новый E-mail адрес</p>
                <input type="text" name="" className="personal__input" placeholder="Новый E-mail адрес" />
                <input type="button" value="COXРАНИТЬ" name="" className="personal__button" />
            </form>


        </div>
    )
}

export default ControlTab
